'use client';

import React from 'react';
import { AppBar, Toolbar, Typography, Button, Box } from '@mui/material';
import { useRouter } from 'next/navigation';

const Navbar = () => {
  const router = useRouter();

  return (
    <AppBar
      position="static"
      sx={{
        backgroundColor: '#0D7D5A',
        height: '70px',
        boxShadow: 'none',
        justifyContent: 'center',
      }}
    >
      <Toolbar
        sx={{
          display: 'flex',
          justifyContent: 'space-between',
          alignItems: 'center',
          px: { xs: 2, md: 4 },
        }}
      >
        <Typography
          variant="h6"
          sx={{
            fontWeight: 600,
            fontSize: '24px',
            color: '#ffffff',
            cursor: 'pointer',
          }}
          onClick={() => router.push('/')}
        >
          Your Portal
        </Typography>

        <Box>
          <Button
            variant="contained"
            sx={{
              backgroundColor: '#ffffff',
              color: '#0D7D5A',
              fontWeight: 'bold',
              textTransform: 'none',
              '&:hover': {
                backgroundColor: '#e0f2f1',
              },
            }}
            onClick={() => router.push('/login')}
          >
            Sign In
          </Button>
        </Box>
      </Toolbar>
    </AppBar>
  );
};

export default Navbar;
